























import Vue from 'vue'
import { mapGetters } from 'vuex'
export default Vue.extend({
  computed: {
    ...mapGetters({ players: 'getPlayers' })
  },
  methods: {
    resume() {
      this.$router.push('/Game');
    },
    restart() {
      this.$store.dispatch('resetScores');
      this.resume();
    },
    setup() {
      this.$router.push('/');
    }
  },
})
